<template>
    <div class="contain">
        <div class="containHead">
            <div id="categoryName">{{$store.state.Community.categoryName}}</div>
            <select @change="sortPost()" name="sort" id="sort" class="sort">
                <option v-for="option in option.value" class="sortPost" :key="option">{{option}}</option>
            </select>
        </div>
        <div class="inner">
            <router-link style="text-decoration: none; color: #333" class="postUrl" :to="postUrl">
                <div class="post" v-for="data, i in post.title.length" :key="i"
                @click="urlChange(post.user_id[i], post.board_id[i], post.post_id[i])">

                    <img :src="post.img[i]" class="titleImg" :style="{backgroundImage: `url(${post.img[i]})`}"/>
                    <!-- <p>{{post.img[i]}}</p> -->
                    <div class="bottom">
                        <p style="display: none"> ID: {{post.post_id[i]}}</p>
                        <h3 class="title"><strong>  : {{post.title[i]}}</strong></h3>
                        <p> /  : {{post.user_id[i]}}</p>
                        <p>: {{post.date[i]}}</p>
                        {{searchRes}}
                        <p>{{$store.state.Search.searchValue}}</p>
                    </div>
                </div>
            </router-link>
            <div class="btnBox">
                <button @click="moreData" class="moreBtn"> </button>
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios';
import {mapState} from 'vuex';
export default {
    //     
    // test   => npx json-server ./exerciseData.json --watch --port 8800
    data(){
        return{
            post: {
                img: [],
                title: [],
                user_id: [],
                post_id: [],
                board_id: [],
                date: [],
                postWrite: [],
                count: 9
            },
            searchRes: this.$store.state.Search.searchValue,
            // postCount: 9,
            category: ['all', 'category1', 'category2', 'category3'],
            postUrl: '/login',
            option: {
                value: ['', ''],
                class: ['up', 'up'],
            }
        }
    },
    computed: {
        ...mapState('Community', ['categoryName', 'categoryState', 'post']),
        ...mapState('Search', ['searchRes']),
    },
    props:{
        propsRes: String,
    },
    watch: {
        // vuex categoryName  getPost(  ) .
        categoryName(){
            this.getPost();
        },
        //    
        propsRes(result){
            // search  ,    post img, title, id   .
            console.log(result);
            this.post.title = [];
            this.post.img = [];
            this.post.date = [];
            this.post.board_id = [];
            this.post.user_id = [];
            this.post.post_id = [];
            axios.get('/api/searchtitle', {
                // .. 
                params: {title: result}}, 
                {withCredentials: true})
            .then(res => {
                console.log('', res.data);
                
                if(result === ''){
                    this.getPost();
                }else{
                    for(let i = 0; i < res.data.length; i++){
                        console.log('  ', res.data[i].title);
                        this.post.board_id.push(res.data[i].board_id);
                        this.post.post_id.push(res.data[i].post_id);
                        this.post.title.push(res.data[i].title);
                        this.post.user_id.push(res.data[i].nickname);
                        this.post.date.push(res.data[i].creation_datetime);
                        this.post.img.push(`http://localhost:3000/img/postPhoto/${res.data[i].photographic_path}`);
                    }
                }
            }).catch(err => console.log(err));
        }

    },
    mounted(){
        console.log(this.$route.name);
        this.postCount = 0;
        this.urlChange();
        this.getPost();
    },
    methods: {
        sortPost(){
            // let sortPost = document.querySelectorAll('.sortPost');
            let sort = document.querySelector('.sort');
            console.log(sort.value);
            if(sort.value === ''){
                axios.get('/api/showPostAsc', {params: {board_id: 1, limit: 0}})
                .then(res => {
                this.post.title = [];
                this.post.id = [];
                this.post.img = [];
                this.post.date = [];
                this.post.board_id = [];
                this.post_id = [];
                for(let i = 0; i < res.data.length; i++){
                    console.log(res);
                    this.post.post_id.push(res.data[i].post_id);
                    this.post.board_id.push(res.data[i].board_id);
                    this.post.title.push(res.data[i].title);
                    this.post.user_id.push(res.data[i].nickname);
                    this.post.date.push(res.data[i].creation_datetime);
                    this.post.img.push(`http://localhost:3000/img/postPhoto/${res.data[i].photographic_path}`);
                    this.post.postCount = res.data.length;
                }
            }).catch(err => console.log(err));
            }else{
                this.getPost();
            }
        },
        /* 
            getPost()  . post.title, id, img    .
                     .
        */
        getPost(){
            let userInformation = JSON.parse(localStorage.getItem("userInformation"));
            this.post.title = [];
            this.post.id = [];
            this.post.img = [];
            this.post.date = [];
            this.post.board_id = [];
            this.post_id = [];
            if(this.$route.name === 'MyPage'){
                console.log('  ');
                axios.get('/api/myPagePost', {params: {nickname: userInformation.nickname, limit: 0}})
                .then(res => {
                    for(let i = 0; i < res.data.length; i++){
                        console.log(res);
                        this.post.post_id.push(res.data[i].post_id);
                        this.post.board_id.push(res.data[i].board_id);
                        this.post.title.push(res.data[i].title);
                        this.post.user_id.push(res.data[i].nickname);
                        this.post.date.push(res.data[i].creation_datetime);
                        this.post.img.push(`http://localhost:3000/img/postPhoto/${res.data[i].photographic_path}`);
                        this.post.postCount = res.data.length;
                    }
                }).catch(err => console.log(err));
            }else{
                console.log('');
                axios.get('/api/showPostDesc',{params: {board_id: 1, limit: 0}})
                .then(res => {
                    console.log(res);
                    this.postCount = res.data.length;
                    for(let i = 0; i < res.data.length; i++){
                        this.post.post_id.push(res.data[i].post_id);
                        this.post.board_id.push(res.data[i].board_id);
                        this.post.title.push(res.data[i].title);
                        this.post.user_id.push(res.data[i].nickname);
                        this.post.date.push(res.data[i].creation_datetime);
                        this.post.img.push(`http://localhost:3000/img/postPhoto/${res.data[i].photographic_path}`);
                    }
                })
                .catch(err => console.log(err));
            }
        },
        urlChange(userId, boardId, postId){
            if(document.cookie){
                this.postUrl = `/${userId}/${boardId}/${postId}`;
            }
        },
        //     .
        moreData(){
            let userInformation = JSON.parse(localStorage.getItem("userInformation"));
            console.log(' ', this.postCount);
            // ex) 9 post   .
            if(this.$route.name === 'MyPage'){
                console.log('  ');
                console.log(this.post.count);
                axios.get('/api/myPagePost', {params: {nickname: userInformation.nickname, limit: this.post.count}})
                .then(res => {
                    for(let i = 0; i < 9; i++){
                        this.post.post_id.push(res.data[i].post_id);
                        this.post.board_id.push(res.data[i].board_id);
                        this.post.title.push(res.data[i].title);
                        this.post.user_id.push(res.data[i].nickname);
                        this.post.date.push(res.data[i].creation_datetime);
                        this.post.img.push(`http://localhost:3000/img/postPhoto/${res.data[i].photographic_path}`);
                    }
                    this.post.count = this.post.count + 9;
                }).catch(err => {
                    this.post.count = this.post.post_id;
                    console.log(err)
                });
            }else{
                console.log(this.post.count);
                axios.get('/api/showPostDesc', {params: {board_id: 1, limit: this.post.count}})
                .then(res => {
                    console.log(res);
                    for(let i = 0; i < 9; i++){
                        this.post.post_id.push(res.data[i].post_id);
                        this.post.board_id.push(res.data[i].board_id);
                        this.post.title.push(res.data[i].title);
                        this.post.user_id.push(res.data[i].nickname);
                        this.post.date.push(res.data[i].creation_datetime);
                        this.post.img.push(`http://localhost:3000/img/postPhoto/${res.data[i].photographic_path}`);
                    }
                    this.post.count = this.post.count + 9;
                }).catch(err => {
                    this.post.count = this.post.post_id;
                    console.log(err)
                })
            }
        }
    }

}
</script>
<style lang="scss" scoped>
.contain{
    position: relative;
    // width: 100vw;
    // width: 25vw;
    height: 300px;
    .containHead{
        position: relative;
        bottom: 50px;
        z-index: 99;
        width: 70vw;
        display: flex;
        justify-content: space-between;
        .sort{
            border: 0;
            margin-right: 30px;
        }
    }
    #categoryName{
        font-size: 30px;
        font-weight: 700;
        margin-top: -20px;
    }
    .inner{
        position: absolute;
        top: 0;
        .postUrl{
            display: flex;
            flex-wrap: wrap;
            // align-items: center;
            justify-content: space-between;
            width: 70vw;
            height: 100%;
            .post{
                width: 300px;
                height: 300px;
                box-shadow: 4px 12px 30px 6px rgb(231, 231, 231);
                margin-right: 20px;
                margin-top: 50px;
                border-radius: 10px;
                cursor: pointer;
                .titleImg{
                    border-radius: 5px;
                    background-position: center;
                    background-repeat: no-repeat;
                    background-size: 100%;
                    width: 100%;
                    height: 50%;
                    background-color: rgb(184, 184, 184);
                    transition: .3s;
                }
                .titleImg:hover{
                    background-size: 120%;
                }
                .bottom{
                    width: 90%;
                    margin: auto;
                }
            }
        }

        .btnBox{
            display: flex;
            justify-content: center;
            align-items: center;
            margin-top: 40%;
            .moreBtn{
                position: fixed;
                bottom: 50px;
                left: 0;
                right: 0;
                margin: auto;
                font-size: 16px;
                font-weight: 700;
                width: 100px;
                height: 40px;
                border-radius: 30px;
                transition: .5s;
                cursor: pointer;
                background-color: #93B5C6;
                color: #fff;
                border: 0;
            }
        }
    }
    @media screen and (max-width: 1000px){
        .containHead{
            // display: block;
            // width: 800px;
            width: 90vw;
            #categoryName{
                font-size: 25px;
                font-display: 700;
            }
        }
        .inner{
            .postUrl{
                display: block;
                .post{
                    width: 120%;
                    .title{
                        font-size: 20px;
                    }
                    p{
                        font-size: 13px;
                        font-weight: 500;
                    }
                }
            }
        }
    }
    // @media screen and (max-width: 768px){
    //     .inner{
    //         display: block;
    //         .post{
    //             width: 90%;
    //         }
    //     }
    // }
}
</style>