<template>
    <div class="contain">
        <div class="inner">
            <div class="text">
                <p class="title"><span class="importantText"></span> <span class="importantText"></span> <span class="importantText"></span>?</p>
                <span class="textInner">
                    <!--  '  '   -->
                            
                      ,  <br/><br/> ,  
                           .
                </span>
                <p class="title">  !</p>
                <span class="textInner">
                             .<br/>
                    <br/>       .
                </span>
                <p class="title">    ?</p>
                <span class="textInner">
                          ..?<br/>
                           !
                </span>
                <!-- <div v-for="title, i in titles.length" :key="i">
                    <p class="title">{{titles[i]}}</p>
                    <span class="textInner">{{textInner[i]}}</span>
                </div> -->
            </div>
            <div class="imgBox">
                <div class="img"/>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data(){
        return{
            titles: [
                '  .',
                '  !',
                '    ?'
            ],
            textInner: [
                `         ,  ,         .`,
                '         .        .',
                '      ..?       !'
            ]
        }
    },
    mounted(){
        this.scrollEvent();
    },
    methods: {
        scrollEvent(){
            //    height 
            let screenHeight = document.documentElement.scrollHeight;
            console.log('   height', screenHeight);


            let img = document.querySelector('.img');
            let title = document.querySelectorAll('.title');
            let textInner = document.querySelectorAll('.textInner');
            document.addEventListener('scroll', function(){
                let currentScrollValue = document.documentElement.scrollTop;
                for(let i = 0; i < title.length; i++){
                    // 1700, 2200 // 3 2
                    if(currentScrollValue > screenHeight / 3.0 && currentScrollValue < screenHeight / 2.2){
                        img.style.backgroundImage = 'url(https://www.canceranswer.co.kr/news/photo/202109/3045_4820_1253.jpg)';
                        title[2].classList.add('event');
                        textInner[1].classList.add('event');
                        // 2200
                    }else if(currentScrollValue > screenHeight / 2.2){
                        img.style.backgroundImage = 'url(https://www.ftimes.kr/news/photo/202110/13545_15200_4650.jpg)';
                        title[3].classList.add('event');
                        textInner[2].classList.add('event');
                    }else{
                        img.style.backgroundImage = 'url(https://www.dementianews.co.kr/news/photo/201902/1501_1270_5524.jpg)';
                        // title[i].classList.remove('event');
                        // textInner[i].classList.remove('event');
                    }
                }
            });
        }
    },
    computed: {
        subTitle(){
            return this.textInner.replace("\n", "<br/>")
        }
    },
    
}
</script>

<style lang="scss" scoped>
.contain{
    width: 100vw;
    .inner{
        position: relative;
        color: #333;
        width: 80vw;
        height: 3000px;
        margin: auto;
        display: flex;
        .text{
            overflow-x: hidden;
            position: absolute;
            right: 0;
            width: 40%;
            margin-bottom: 20%;
             .title{
                margin-top: 80%;
                font-size: 50px;
                font-weight: 900;
                transition: .5s;
            }
            .textInner{
                font-size: 30px;
                font-weight: 500;
                line-height: 180%;
                transition: .5s;
                // margin-top: 10%;
            }
            .title:nth-child(3), .textInner:nth-child(4),
            .title:nth-child(5), .textInner:nth-child(6){
                opacity: 0;
                transform: translateX(200px);
            }
            .title:nth-child(3).event, .textInner:nth-child(4).event,
            .title:nth-child(5).event, .textInner:nth-child(6).event{
                opacity: 1;
                transform: translateX(0px);
            }   
            // }
        }
        .img{
            position: sticky;
            border-radius: 20px;
            // background-image: url('https://www.dementianews.co.kr/news/photo/201902/1501_1270_5524.jpg');
            background-size: cover;
            transition: .3s;
            top: 100px;
            width: 500px;
            height: 400px;
            margin-top: 20%;
        }
    }
    @media screen and (max-width: 1050px){
        .inner{
            .text{
                padding-top: 40%;
                .title{
                    font-size: 40px;
                }
                .textInner{
                    font-size: 20px;
                }
            }
            .img{
                width: 40vw;
                height: 50vh;
            }
        }
    }
    @media screen and (max-width: 768px){
        .inner{
            .text{
                padding-top: 130%;
                width: 80vw;
                .title{
                    font-size: 30px;
                }
                .textInner{
                    font-size: 20px;
                }
            }
            .img{
                width: 80vw;
                height: 40vh;
            }
        }
    }   
    @media screen and (max-width: 565px){
        .inner{
            .text:nth-child(2){
                margin-top: 30%;
            }
            .img{
                width: 80vw;
            }
        }
    }
}
</style>