export const Search = {
    namespaced: true,
    state: () => ({
        searchValue: '',
        //  input   
        searchRes: '',
    }),
    mutations: {
        searchChange(state, {inputRes}){
            state.searchRes = inputRes;
        }
    },
    actions: {
        searchAction(context, payload){
            const {inputRes} = payload;
            context.searchRes = inputRes;
            context.commit('searchChange', {inputRes});
        }
    }
}